import React from 'react';
import { Route, Routes } from 'react-router-dom';

const DepositCheckStart = React.lazy(() => import('./DepositCheckStart/DepositCheckStart'));
const DepositCheckRequirements = React.lazy(() => import('./DepositCheckRequirements/DepositCheckRequirements'));
const CheckDetailsConfirm = React.lazy(() => import('./CheckDetailsConfirm/CheckDetailsConfirm'));
const DepositCheckSuccess = React.lazy(() => import('./DepositCheckSuccess/DepositCheckSuccess'));
const DepositCheckStatus = React.lazy(() => import('./DepositCheckStatus/DepositCheckStatus'));

function AoRouter() {
  return (
    <Routes>
      <Route
        path=""
        element={(<DepositCheckStart />)}
      />
      <Route
        path="form"
        element={(<DepositCheckRequirements />)}
      />
      <Route
        path="confirm"
        element={(<CheckDetailsConfirm />)}
      />
      <Route
        path="success"
        element={(<DepositCheckSuccess />)}
      />
      <Route
        path="status"
        element={(<DepositCheckStatus />)}
      />
    </Routes>
  );
}

export default AoRouter;
